body {
  font-family: 'Raleway', sans-serif;
  width: 35%;
  margin-left: auto;
  margin-right: auto;
  align-content: center;
  text-align: center;
  color: #333;
}

.container {
  border: 4px solid #77ffe2;
  padding: 15px 60px;
  background-color: #b9fff0;
  height: 650px;
  border-radius: 50px 0 50px 0;
}

.title {
  background-color: #333;
  color: #eee;
  padding: 15px;
}

ul {
  line-height: 1.5em;
  text-align: left;
}

button {
  font-family: 'Raleway', sans-serif;
  height: 45px;
  font-weight: 700;
  width: auto;
  border-radius: 0 7px 7px 0;
  border: 4px solid #8850ff;
  color: #fff;
  background-color: #5e00e4;
}

form {
  font-family: 'Raleway', sans-serif;
  height: 45px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  align-items: stretch;
  border-top: 3px solid #fff;
  padding-top: 10px;
  /* padding: 16px 10px; */
}

.startMessage {
  margin-right: 15px;
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 700;
  align-self: center;
  text-align: right;
}

.message {
  background-color: ;
  margin: 20px 0 0 0;
  font-size: 1.5em;
  line-height: 1.75em;
}

.guess-field {
  font-family: 'Raleway', sans-serif;
  border: 2px solid #eee;
  padding-left: 10px;
  color: #666;
  border-radius: 7px 0 0 7px;
}

.timer {
  font-size: 3.5em;
  font-weight: 700;
  color: #656b77;
  margin: 0;
}

.info {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.info>p {
  margin: 0 10px;
}

.count {
  font-size: 2em;
}
